.section {
    padding: 70px 0;
    position: relative;

    .row + .category {
        margin-top: $margin-base-vertical;
    }

    .carousel {
        width: 100%;
    }
}

.path {
    position: absolute;
    opacity: 0.02;
}

.dots {
    position: absolute;
    opacity: 0.07;
    z-index: 2;
}

.shapes {
    position: absolute;
    opacity: 0.02;
    z-index: 2;
}

.main {
    position: relative;
    width: $full-width;
}

.section-navbars {
    padding-bottom: 0;
    overflow: hidden;
    .navbar {
        z-index: 2;
    }

    img.path {
        left: -400px;
    }
}

.section-typo {
    overflow: hidden;
    .path {
        right: -400px;
    }

    .path1 {
        bottom: 5%;
        width: 70%;
        left: -200px;
    }
}

.section-pagination {
    .path {
        width: 65%;
        right: 0%;
        top: 48%;
    }

    .path1 {
        width: 30%;
        top: 0;
        left: 3%;
    }
}

.section-javascript {
    overflow: hidden;
    .path {
        right: -300px;
        width: 80%;
    }

    .path1 {
        width: 25%;
        left: 3%;
        top: 0;
    }
}

.section-full-screen {
    height: 100vh;
}
.section-signup {
    padding-top: 20vh;
    overflow: hidden;

    .square {
        background-size: cover;
    }
    .square-1 {
        height: 300px;
        width: 300px;
        background-image: url("../img/square1.png");
        top: 80px;
        left: 100px;
        opacity: 0.3;
        animation-duration: 5s;
    }

    .square-2 {
        height: 200px;
        width: 200px;
        background: url("../img/square3.png");
        background-size: cover;
        bottom: 40px;
        left: 600px;
        opacity: 0.1;
        animation-duration: 8s;
    }

    .square-3 {
        height: 100px;
        width: 100px;
        background: url("../img/square2.png");
        background-size: cover;
        top: 300px;
        left: 700px;
        animation-duration: 5s;
    }

    .square-4 {
        height: 250px;
        width: 250px;
        background: url("../img/square3.png");
        background-size: cover;
        top: 0px;
        right: 100px;
        opacity: 0.8;
        animation-duration: 9s;
    }
}

.parallax-s {
    overflow: hidden;
    height: 500px;
    width: 100%;
}

.section-image {
    background-size: cover;
    background-position: center center;
    position: relative;
    width: 100%;

    .title,
    .card-plain .card-title {
        color: $white;
    }

    .nav-pills .nav-link {
        background-color: $white;

        &:hover,
        &:focus {
            background-color: $white;
        }
    }

    .info-title,
    .info.info-horizontal .icon i,
    .card-pricing.card-plain ul li {
        color: $white;
    }
    .description,
    .info .icon:not(.icon-circle) {
        color: $opacity-8;
    }
    .card:not(.card-plain) {
        .info-title {
            color: $black;
        }
        .info p,
        .info .icon,
        .description {
            color: $dark-gray;
        }
    }

    .footer {
        color: $white;
    }
    .card-plain {
        [class*="text-"],
        ul li b {
            color: $white;
        }
        .category {
            color: rgba($white, 0.5);
        }
    }

    &:after {
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 100%;
        display: block;
        left: 0;
        top: 0;
        content: "";
        background-color: rgba(0, 0, 0, 0.7);
    }

    .container {
        z-index: 2;
        position: relative;
    }
}

.page-header {
    min-height: 100vh;
    max-height: 999px;
    padding: 0;
    color: $white;
    position: relative;
    overflow: hidden;

    .page-header-image {
        position: absolute;
        background-size: cover;
        background-position: center center;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    & > .content {
        margin-top: 11%;
        text-align: center;
        margin-bottom: 50px;
    }

    .signup-page & {
        max-height: fit-content;
    }

    .content-center {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 2;
        -ms-transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        text-align: center;
        color: $white;
        padding: 0 15px;
        width: 100%;
        max-width: 880px;

        &.brand {
            width: 47%;
        }
    }

    footer {
        position: absolute;
        bottom: 0;
        width: 100%;
    }

    .container {
        height: 100%;
        z-index: 1;
    }

    .category,
    .description {
        color: $opacity-8;
    }

    &.page-header-small {
        min-height: 60vh;
        max-height: 440px;
    }

    &.page-header-mini {
        min-height: 40vh;
        max-height: 340px;
    }

    .title {
        margin-bottom: 15px;
    }
    .title + h4 {
        margin-top: 10px;
    }

    &.header-filter:after {
        background: rgba(0, 0, 0, 0.5);
    }
}

.clear-filter {
    &:after,
    &:before {
        display: none;
    }
}

.section-basic {
    overflow: hidden;
    .category {
        margin-bottom: 10px;
    }

    img.path {
        right: -400px;
    }
}

.section-story-overview {
    padding: 50px 0;

    .image-container {
        height: 335px;
        position: relative;
        background-position: center center;
        background-size: cover;
        box-shadow: $box-shadow-raised;
        border-radius: 0.25rem;

        & + .category {
            padding-top: 15px;
        }

        &.image-right {
            z-index: 2;

            + h3.title {
                margin-top: 120px;
            }
        }

        &.image-left {
            z-index: 1;
        }

        img {
            width: 100%;
            left: 0;
            top: 0;
            height: auto;
            position: absolute;
        }

        &:nth-child(2) {
            margin-top: 420px;
            margin-left: -105px;
        }
    }

    p.blockquote {
        width: 220px;
        min-height: 180px;
        text-align: left;
        position: absolute;
        top: 376px;
        right: 155px;
        z-index: 0;
    }
}

.section-coins {
    margin-top: 200px;

    h1 {
        margin-bottom: 150px;
    }
}

.section-nucleo-icons {
    --icon-size: 5rem;
    --icon-sm-size: 3.75rem;
    --gutter: 7rem;
    overflow: hidden;

    .title,
    .description,
    .btn-wrapper {
        text-align: center;
    }

    .btn-wrapper {
        margin: 3rem auto;
    }

    .icons-container {
        position: relative;
        z-index: 1;
        max-width: 100%;
        height: 360px;
        margin: 0 auto;
        i {
            position: absolute;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            background: $white;
            z-index: 1;
            transform: translate(-50%, -50%);
            @include box-shadow($box-shadow);
            transition: all 0.2s cubic-bezier(0.25, 0.65, 0.9, 0.75);

            &.icon {
                width: var(--icon-size);
                height: var(--icon-size);
                font-size: 2em;
            }

            &.icon-sm {
                width: var(--icon-sm-size);
                height: var(--icon-sm-size);
                font-size: 1.7em;
            }

            &:nth-child(1) {
                font-size: 42px;
                z-index: 2;
            }
        }

        &:not(.on-screen) {
            i {
                transform: translate(-50%, -50%);
                left: 50%;
                top: 50%;

                &:not(:nth-child(1)) {
                    opacity: 0;
                }
            }
        }

        &.on-screen {
            i {
                opacity: 1;

                &:nth-child(1) {
                    left: 50%;
                    top: 50%;
                    font-size: 42px;
                }

                &:nth-child(2) {
                    left: calc(50% + (var(--gutter) * 1.7));
                    top: 50%;
                }

                &:nth-child(3) {
                    left: calc(50% + var(--gutter));
                    top: calc(50% + var(--gutter));
                }

                &:nth-child(4) {
                    left: calc(50% + var(--gutter));
                    top: calc(50% - var(--gutter));
                }

                &:nth-child(5) {
                    left: calc(50% + (var(--gutter) * 4));
                    top: 50%;
                }

                &:nth-child(6) {
                    left: calc(50% + (var(--gutter) * 2.7));
                    top: calc(50% + (var(--gutter) * 1.5));
                }

                &:nth-child(7) {
                    left: calc(50% + (var(--gutter) * 2.7));
                    top: calc(50% - (var(--gutter) * 1.5));
                }

                &:nth-child(8) {
                    left: calc(50% - (var(--gutter) * 1.7));
                    top: 50%;
                }

                &:nth-child(9) {
                    left: calc(50% - var(--gutter));
                    top: calc(50% + var(--gutter));
                }

                &:nth-child(10) {
                    left: calc(50% - var(--gutter));
                    top: calc(50% - var(--gutter));
                }

                &:nth-child(11) {
                    left: calc(50% - (var(--gutter) * 4));
                    top: 50%;
                }

                &:nth-child(12) {
                    left: calc(50% - (var(--gutter) * 2.7));
                    top: calc(50% + (var(--gutter) * 1.5));
                }

                &:nth-child(13) {
                    left: calc(50% - (var(--gutter) * 2.7));
                    top: calc(50% - (var(--gutter) * 1.5));
                }
            }
        }
    }

    .path {
        left: -300px;
        top: 0;
        width: 80%;
    }
}

.section-examples {
    .path {
        width: 55%;
    }

    .container .col-sm-6 img {
        border: 1px solid $primary;
    }
}

.section-download {
    overflow: hidden;
    .path {
        right: -400px;
    }
}

// Blurable sections

.blur-hover {
    position: relative;
    padding-bottom: 100px;

    .blur-item {
        transition: 1s cubic-bezier(0.19, 1, 0.22, 1);
        will-change: transform;
        filter: blur(0);
        opacity: 1;
    }

    .blur-hidden {
        position: absolute;
        top: calc(50% + 7px);
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: $transition-base;
        z-index: 100;
    }
    &:hover {
        .blur-item {
            opacity: 0.8;
            filter: blur(10px);
            transform: scale(0.95);
            z-index: 1;
        }
        .blur-hidden {
            opacity: 1;
            top: 50%;
        }
    }
}

.section-images {
